.navbar-container{
    position: absolute;
    width: 100%;
    z-index: 10;
    transition: all 1.7s;
}


.navbar-image{
    height: 3.5rem;
    border: black 1px solid;
    box-shadow: rgba(30, 144, 255, 0.71) 0px 0px 10px 1px;
    border-radius: 50%;
}

.nav-link{
    text-decoration: none;
    color: #fff9;
    font-size: 1.2rem;
    transition: all 0.3s;
}

.nav-link:hover{
    text-decoration: none;
    color: #fffb !important;
    font-size: 1.25rem;
    transition: all 0.3s;
}

.navbar-container:hover{
    /*background-color: #fff1;*/
    box-shadow: rgba(30, 144, 255, 0.71) 0 0 10px 1px;
    transition: all 0.7s;
}